import {Ref} from 'vue';
import {HeaderName} from '../grid/Common';

interface SetupSelectedColumnsOptions {
	columnDisplayStates: Ref<Record<HeaderName, boolean>>;
}

export function SetupSelectedColumns(options: SetupSelectedColumnsOptions) {
	const {columnDisplayStates} = options;

	// function toggleColumnDisplayState(columnName: HeaderName) {
	// 	const state = columnDisplayStates.value[columnName];
	// 	if (state == null) {
	// 		columnDisplayStates.value[columnName] = true;
	// 	} else {
	// 		columnDisplayStates.value[columnName] = !state;
	// 	}
	// }
	function toggleHeaderName(headerName: HeaderName) {
		const state = columnDisplayStates.value[headerName];
		if (state == null) {
			columnDisplayStates.value[headerName] = true;
		} else {
			columnDisplayStates.value[headerName] = !state;
		}
	}
	function selectAllHeaderNames() {
		const headerNames = Object.keys(columnDisplayStates.value) as HeaderName[];
		for (const headerName of headerNames) {
			columnDisplayStates.value[headerName] = true;
		}
	}
	function selectNoHeaderNames() {
		const headerNames = Object.keys(columnDisplayStates.value) as HeaderName[];
		for (const headerName of headerNames) {
			columnDisplayStates.value[headerName] = false;
		}
	}

	return {
		// toggleColumnDisplayState,
		toggleHeaderName,
		selectAllHeaderNames,
		selectNoHeaderNames,
	};
}
