import {Object3D} from 'three';
import {TreeElement} from './TreeElement';

// export interface SceneTreeUtils {
// 	treeElementByObject3D: Map<Object3D, TreeElement>;
// 	object3DByTreeElement: Map<TreeElement, Object3D>;
// }

class SceneTreeUtilsClass {
	private _treeElementByObject3D: Map<string, TreeElement> = new Map();
	private _object3DByTreeElement: Map<string, Object3D> = new Map();

	clear() {
		this._treeElementByObject3D.clear();
		this._object3DByTreeElement.clear();
	}

	// addObjectAsScene(object: Object3D, treeElement: TreeElement) {
	// 	this._treeElementByObject3D.set(object.uuid, treeElement);
	// }
	addObject(object: Object3D, treeElement: TreeElement) {
		this._treeElementByObject3D.set(object.uuid, treeElement);
		this._object3DByTreeElement.set(treeElement.uuid, object);
	}
	getTreeElement(object: Object3D) {
		return this._treeElementByObject3D.get(object.uuid);
	}
	getObject(treeElement: TreeElement) {
		return this._object3DByTreeElement.get(treeElement.uuid);
	}
}

export const SceneTreeUtils = new SceneTreeUtilsClass();
