import {ICellRendererComp, ICellRendererParams} from '@ag-grid-community/core';
// import {Object3D} from 'three';
import {TreeElement} from '../TreeElement';
import {SceneTreeUtils} from '../SceneTreeUtils';

type PropertyName = 'visible' | 'matrixAutoUpdate';
abstract class CheckboxCellRenderer implements ICellRendererComp {
	eGui!: HTMLSpanElement;
	abstract propertyName: PropertyName;

	// private static _object3DByTreeElement: Map<TreeElement, Object3D> | undefined;
	// static setObject3DByTreeElement(map: Map<TreeElement, Object3D>) {
	// 	this._object3DByTreeElement = map;
	// }

	// init method gets the details of the cell to be renderer
	private _container = document.createElement('div');
	init(params: ICellRendererParams) {
		this.eGui = document.createElement('span');
		const treeElement = params.data as TreeElement;
		this._container.classList.add('booleanProp-checkbox');
		this.eGui.append(this._container);

		this._updateContent(treeElement);

		this._container.addEventListener('pointerdown', () => {
			treeElement[this.propertyName] = !treeElement[this.propertyName];
			const object = SceneTreeUtils.getObject(treeElement);
			if (object) {
				object[this.propertyName] = treeElement[this.propertyName];
			} else {
				console.warn('object not found', treeElement, SceneTreeUtils);
			}
			this.refresh(params);
		});
	}

	private _updateContent(treeElement: TreeElement) {
		const content = treeElement[this.propertyName] ? '✅' : '❌';
		this._container.innerText = content;
	}

	getGui() {
		return this.eGui;
	}

	refresh(params: ICellRendererParams): boolean {
		const treeElement = params.data as TreeElement;
		this._updateContent(treeElement);
		return true;
	}
}

export class CheckboxCellRendererVisible extends CheckboxCellRenderer {
	propertyName: PropertyName = 'visible';
}
export class CheckboxCellRendererMatrixAutoUpdate extends CheckboxCellRenderer {
	propertyName: PropertyName = 'matrixAutoUpdate';
}
